const headers = [
    { label: "Id", key: "Id" },
    { label: "Manufacturer", key: "ManufacturerName" },
    { label: "Quantity", key: "Quantity" },
    { label: "Delivery Date", key: "DeliveryDate"}
  ];
class DisplayItems extends React.Component {
    
    constructor(props){
        super(props);
        this.state = {
            "fullData":[],
            "items" : [],
            "loadingScreen":false,
            "toastOpen":false
        }
        
    }
    //This lifecycle hook is to trigger GET service, update state property.
    componentDidMount(){
        //If token is not present,Re direct to home page.
        if(!this.context.token) {
            this.props.history.push("/");
        }else{
            this.setState({"loadingScreen":true});
        const headers = {
            'Content-Type': 'application/json',
            'Authorization': this.context.token
        }
        //TEMP Fix:Static string provided as query param.
        axios.get(Config.URL.getDeliveryItemsURL+"?ManufacturerName=COMMSCOPE",{headers})
        .then( (response) =>{
                if(response && response.status == 200 && response.statusText =="OK"){
                    this.setState({"fullData":JSON.parse(response.data)});
                    this.setState({"items":JSON.parse(response.data)});
                }
            })
        .catch( (error) =>{
               this.setState({"toastOpen":true},()=>{
                   setTimeout(()=>{
                    this.setState({"toastOpen":false});
                   },5000);
               })
            })
        .finally(()=>{
                this.setState({"loadingScreen":false});
            });
        }
        
    }
    handleSearch =(event) =>{
        let temp=[];
        if(event.target.value){
            this.state.fullData.map((item)=>{
                if((item.ManufacturerName.toUpperCase()).indexOf(event.target.value.toUpperCase()) > -1){
                    temp.push(item);
                }
            });
            this.setState({"items":temp});
        }else{
            this.setState({"items": this.state.fullData});
        }
    }
    //Function for export as PDF functionality.
    exportPDF = () => {
        const unit = "pt";
        const size = "A4"; 
        const orientation = "portrait"; 
        const marginLeft = 40;
        const doc = new jsPDF(orientation, unit, size);
        doc.setFontSize(15);
        const title = "REPORT";
        const headers = [["Id", "Manufacturer","Quantity","Delivery Date"]];
        const data = this.state.items.map(elt=> [elt.Id, elt.ManufacturerName,elt.Quantity,elt.DeliveryDate]);
        let content = {
          startY: 50,
          head: headers,
          body: data
        };
        doc.text(title, marginLeft, 40);
        doc.autoTable(content);
        doc.save("report.pdf")
      }
    render(){
        return (
            <Consumer>
            {() =>
                <div className="content_wrapper">
                    {this.state.toastOpen ? <div className="p-3  my-2 rounded toast-container bg-danger">
                        <Toast >
                        <ToastHeader>
                        Message
                        </ToastHeader>
                        <ToastBody>
                            Network Error:Failed to get data
                        </ToastBody>
                        </Toast>
                        </div>:null}
                     
            {this.state.loadingScreen ? <div className="load-overlay" ><Spinner className="mtopfix" style={{ width: '6rem', height: '6rem' }} type="grow" color="dark" /></div> : null}   
                <div className="container pt-5 pb-5">
                <div className="global-filter-holder">
                <div className="input-group mb-3">
                <input type="text" className="form-control" onChange={this.handleSearch} placeholder="Search..." aria-label="Recipient's username" aria-describedby="basic-addon2"/>
                <div className="input-group-append">
                <span className="input-group-text" id="basic-addon2"><FontAwesomeIcon  className="fa-lg  ml-2" icon={faSearch} title=""/></span>
                </div>
                </div> 
                </div>
            <div className="card list-wrapper">
            <h6 className="card-header defColor">VIEW MATERIAL REQUEST 
            <button className="export-btn export-csv">
            <CSVLink data={this.state.items} headers={headers} filename={"AllItems.csv"} className="">
            <FontAwesomeIcon  className="fa-lg plusIcon ml-2" icon={faFileExcel} title="Export As CSV"/>
            </CSVLink>
            </button>
            <button className="export-btn export-pdf" onClick={() => this.exportPDF()}>
            <FontAwesomeIcon  className="fa-lg plusIcon ml-2" icon={faFilePdf} title="Export As PDF"/>
            </button>
            </h6>
            <div className="card-body p-0">
            <DataTable className="display-table" columns={columns} data={this.state.items} pagination 
            paginationPerPage = {5} paginationRowsPerPageOptions={[5,10,15]} />
            </div>
            </div></div>
            
            </div>}
            </Consumer>
            )
        }
        
    }
    DisplayItems.contextType  = AppContext;
    export default DisplayItems;